@import url('../types/colorType.css'); /* colorType.css를 가져옴 */


.topBox {
    position: absolute; /* 뷰포트를 기준으로 절대 위치 */
    top: 40%;           /* 수직 중앙 */
    left: 50%;          /* 수평 중앙 */
    transform: translate(-50%, -50%); /* 정확히 중앙으로 이동 */
    width: 330px;
    height: 300px;
}

.markerBox{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: white;
    border-radius: 8px;
    box-shadow: 0 2px 12px rgba(0,0,0,0.2);
    z-index: 1000;
    width: 80%;
    height: 100%;
    display: flex;
}

.smallLeftNav{
    position: absolute;
    background : var(--color-base);
    top:50%;
    left:0%;
    transform: translateY(-50%); /* 수직 중앙 정렬 */
    width : 10%;
    height : 60%;
    border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;
    display: flex;
  justify-content: center;
  align-items: center;
}   

.smallRightNav{
    position: absolute;
    background : var(--color-base);
    top:50%;
    left:90%;
    transform: translateY(-50%); /* 수직 중앙 정렬 */
    width : 10%;
    height : 60%;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    display: flex;
  justify-content: center;
  align-items: center;
    
}   

.exitButton{
    position : fixed;
    top : 3%;
    right : 3%;
    background : black;
    border-radius: 8px;
    width : 20px;
    height : 20px;
}

.pictureBox {
    width: 100%;
    height: auto; /* 높이를 자동으로 설정 */
    aspect-ratio:  3 / 2;
    object-fit: cover; /* 비율을 유지하면서 이미지 잘림 */
    display: block;
    border-top-right-radius: 8px;
    border-top-left-radius: 8px;
  }
  
  
  /* GoogleMapStyle.css */
.custom-cluster {
    background-color: #cf7f77;
    color: rgb(136, 45, 45);
    font-size: 14px;
    font-weight: bold;
    border-radius: 50%;
    border: 2px solid rgb(136, 45, 45);
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 2px 6px rgba(0,0,0,0.3);
  }
  
  .detailBox {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 350px;
    height: 440px;
    background-color: white;
    box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.3);
    z-index: 15;
  }
  
  .detailPicture{
    width: 100%;
    height: auto; /* 높이를 자동으로 설정 */
    aspect-ratio: 3 / 2;
    display: inline-flex;
  }
  .pictureDetailBox{
    width: 100%;
    height: auto; /* 높이를 자동으로 설정 */
    aspect-ratio: 3 / 2;
    object-fit: cover; /* 비율을 유지하면서 이미지 잘림 */
    display: block;
  }

  .pictureNav {
    position: absolute;
    top: 35%;
    transform: translateY(-50%);
    font-size: 32px;
    z-index: 20;
    color: aliceblue; /* 잘 보이도록 */
    cursor: pointer;
  }
  .pictureNav:first-of-type {
    left: 10px;
  }
  .pictureNav:last-of-type {
    right: 10px;
  }
  .contextBox{
    overflow-y: scroll;
    box-sizing: border-box;
    top: 330px; /* 부모 DIV의 100px 아래에서 시작 */
    width: 100%;
    padding-left : 5px;
    padding-right: 5px;
    bottom:0; 
    position:absolute;
    line-height: 1.6;
    padding-top: 7px; /* 위쪽 여백을 추가 */

  }

  .smallNavIcon {
    font-size: 30px;
    vertical-align: middle;
    color: aliceblue;
  }
  
  .contextBox::-webkit-scrollbar {
    width: 10px;
  }
  .contextBox::-webkit-scrollbar-thumb {
    background: var(--color-top); 
    border-radius: 10px; 
  }

  